import { createSlice,createAsyncThunk } from '@reduxjs/toolkit'


export const getapi=createAsyncThunk("get/post",async()=>{
return fetch('https://jsonplaceholder.typicode.com/users').then((response)=>response.json())
})

const getslice=createSlice({
    name:"gets",
    initialState:{
        gets:[],
        loading:false,
        Name:'',
        Email:'',
        Password:''
    },
    extraReducers:{[getapi.pending]:(state,action)=>{
        state.loading=true
    },
    [getapi.fulfilled]:(state,action)=>{
        state.loading=false;
        state.gets=action.payload
    },
    [getapi.rejected]:(state,action)=>{
        state.loading=false
    },},
    reducers:{        
        setName:(state,action)=>{state.Name=action.payload},
        setEmail:(state,action)=>{state.Email=action.payload},
        setPassword:(state,action)=>{state.Password=action.payload},
    }
})

export default getslice.reducer
export const {setName,setEmail,setPassword}= getslice.actions
